import React from 'react';
import './css/mainstyle.css';
import './css/style-text.css';
import Typewriter from 'typewriter-effect';
import { Link } from "react-router-dom";
function MainPage() {
    function TypeWriterHeaderComponent() {
        return <Typewriter
            onInit={(typewriter) => {
            }}
            options={{
                strings: 'Hi there! I\'m Cosmin.',
                cursor: '',
                delay: 70,
                deleteSpeed: 0,
                loop: false,
                autoStart: true
            }}
        />
    }
    function TypeWriterSubheaderComponent() {
        return <Typewriter
            onInit={(typewriter) => {
                typewriter.typeString('')
                    .pauseFor(3000)
                typewriter.typeString('What were you looking for?')
                    .pauseFor(7000)
                    .deleteChars(26)
                typewriter.typeString('There\'re only two buttons... <br> Not hard to pick one')
                    .pauseFor(7000)
                    .deleteChars(50)
                typewriter.typeString('You know... <br> the background is interactable!')
                    .pauseFor(7000)
                    .deleteChars(44)
                typewriter.typeString('Still haven\'t decided? <br> Ok, I\'ll help you out.')
                    .pauseFor(7000)
                    .deleteChars(46)
                typewriter.typeString('The first will take you to <br> things like experience, hobbies and projects...')
                    .pauseFor(7000)
                    .deleteChars(75)
                typewriter.typeString('The other one goes to my blog:<br>personal lessons, things I find motivating, or general tech.')
                    .pauseFor(7000)
                    .deleteChars(90)
                typewriter.typeString('So... which will it be?')
                    .start()
                    .pauseFor(7000);
            }}
            options={{
                delay: 50,
                deleteSpeed: 10,
                loop: true,
                autoStart: true
            }}
        />
    }
    function HomeButton(props) {
        return <div className="mainButton mainbutton-text">
            < Link to={props.destination} >
                {props.buttonText}
            </Link >
        </div>


    }
    return (
        <div className="MainPage">
            <div className="mainContent">
                <div className="mainHeader header-text">
                    <TypeWriterHeaderComponent />
                </div>
                <div className='mainSubheader subheader-text'>
                    <TypeWriterSubheaderComponent />
                </div>
                <div className='mainButtonsSection'>
                    <HomeButton buttonText="About me" destination="/about" />
                    <HomeButton buttonText="Blog" destination="/blog" />
                </div>
            </div>
        </div>
    )
}
export default MainPage;