import React from "react";
import { Link } from "react-router-dom";
import './css/blogstyle.css';
import './css/style-text.css';
function Blog() {
  function BlogCard(props) {
    return <a href={props.link} target="_blank" rel="noopener noreferrer" className={`blogCard`}>
      <div className="blogCardContent">
        <div className="blogCardHeader">
          <div className={`blogCardHeaderLine ${props.cardThemeBands}`}>

          </div>
          <img className="blogCardHeaderPhoto" src={props.imgPath} alt="Article">
          </img>
          <div className={`blogCardHeaderDate ${props.cardThemeSecondary}`}>
            {props.articleDate}
          </div>
        </div>
        <div className="blogCardTitle">
          <div className={`blogCardTitleText ${props.cardTheme}`}>
            {props.articleTitle}
          </div>
          <div className={`blogCardTitleTags ${props.cardThemeSecondary}`}>
            {props.articleTags}
          </div>
        </div>
        <div className={`blogCardDescription ${props.cardTheme}`}>

          {props.articleDescription}

        </div>
        <div className={`blogCardFooter ${props.cardThemeBands}`}>

        </div>
      </div>
    </a>
  }
  return (

    <div className="blogWrap">

      <div className="blogTitle">
        <div className="blogTitleButton">
          <Link to='/home'>
            <div className="smalltxt">Home</div>
            <img src={require('./svg/ArrowDark.svg').default} className="blogArrow" alt="arrowImg"></img>
          </Link>
        </div>
        <div className="blogTitleText">
          Blog
        </div>
        <div className="blogTitleButton">
          <Link to='/about'>
            <div className="smalltxt">About</div>
            <img src={require('./svg/ArrowDark.svg').default} className="blogArrowReverse" alt="arrowImg"></img>
          </Link>
        </div>

      </div>
      <div className="blogCards">
        <BlogCard imgPath={require("./img/img.jpg").default} cardTheme="text-paleTurquoise" cardThemeSecondary="text-brightTurquoise" cardThemeBands="bg-sadTurquoise" articleDate="20/04/2022" articleTitle="Coming soon" articleTags="Web" articleDescription="Nothing here yet " link="#" />
      </div>
    </div>
  )
}


export default Blog;